.navigator {
  height: 10vh;
  margin: 0;
  padding: 0;
  list-style-type: none;
  top: 0;
  left: 0;
  overflow: hidden;
}

.navigator__element {
  float: right;
  height: 100%;
}

.navigator__element_type_home {
  float: left;
  color: red;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: 20pt;
}

.navigator__link {
  height: inherit;
  color: #000;
  text-align: center;
  padding: 16px;
  text-decoration: none;
  display: block;
}

.navigator__link:hover {
  background-color: #ffb01e;
}

.navigator__link:visited {
  color: #000;
}
